svg{
  transition: fill 0.3s ease-in-out;

  &:hover{
    fill: var(--white);
  }

  &:focus{
    fill: var(--white);
  }

  &:active{
    fill: var(--cerulean-blue);
  }
}

.social-svg{
  width: 15px;
  height: 15px;
  fill: #686868;
}

.like-svg{
  fill: var(--white);
  width: 26px;
  height: 26px;
}

.like-transform{
  transform: rotate(180deg);
}

.search-svg{
  fill: var(--cerulean-blue);
  width: 20px;
  height: 20px;
}

.svg-w{
  width: 22px;
  height: 22px;
}

.navigation-prew{
  position: relative;

  &::after{
    content: url(../img/svg/Arrow3.svg);
  }
}

.navigation-next{
  position: relative;

  &::after{
    content: url(../img/svg/Arrow2.svg);
  }
}

.play-svg{
  fill: var(--white);
  width: 20px;
  height: 20px;
}


@media (max-width: 1400px){

  .svg-w {
    width: 20px;
    height: 20px;
  }

  .like-svg{
    width: 16px;
    height: 16px;
  }
}
